<template>
  <section class="person-table-drawer-container">
    <a-drawer
      width="600"
      :title="title"
      :visible="visible"
      :bodyStyle="{ height: 'calc(100% - 55px)' }"
      :maskClosable="false"
      :getContainer="false"
      :destroyOnClose="false"
      @close="doClose()"
    >
      <div
        class="drawer-body-query"
        :style="{ width: '100%', height: '52px', padding: '0 15px' }"
      >
        <a-form
          layout="inline"
          label-width-auto
        >
          <a-row :gutter="20">
            <a-col :sm="12">
              <a-form-item label="">
                <a-input-search
                  v-model="queryParam.personName"
                  allowClear
                  @search="doTableQuery"
                />
              </a-form-item>
            </a-col>
            <a-col
              :sm="10"
              :offset="1"
            >
              <a-form-item label="">
                <a-select
                  v-model="queryParam.isUser"
                  allowClear
                  @change="doTableQuery"
                >
                  <a-select-option value="Y"></a-select-option>
                  <a-select-option value="N"></a-select-option>
                </a-select>
              </a-form-item>
            </a-col>
          </a-row>
        </a-form>
      </div>

      <div
        class="drawer-body-table"
        :style="{ width: '100%', height: 'calc(100% - 52px)' }"
      >
        <s-table
          ref="table"
          :data="loadData"
          :scroll="scroll"
          :columns="columns"
          :customRow="rowClick"
          :rowSelection="rowSelection"
          :rowClassName="rowClassName"
          :clearSelection="clearSelection"
          :showPagination="showPagination"
          :pagination="pagination"
          :immediate="immediate"
          :bordered="bordered"
          rowKey="personSysId"
          style="height: 100%"
          striped
        >
          <template #orgId="key">
            <s-ellipsis
              :tooltip="true"
              :sheared="false"
              :length="10"
            >
              {{ takeLabelByKey(queryOptions.orgTree, key, ['shortName', 'label']) }}
            </s-ellipsis>
          </template>

          <template #isUser="value">
            <a-tag :color="value === 'Y' ? '#ff5500' : '#87d068'">
              {{ value === 'Y' ? '' : '' }}
            </a-tag>
          </template>
        </s-table>
      </div>

      <!--  -->
      <div class="drawer-footer">
        <div class="footer-fixed">
          <a-button @click="doClose()"></a-button>
          <a-button
            type="primary"
            @click="doSubmit()"
          ></a-button>
        </div>
      </div>
    </a-drawer>
  </section>
</template>

<script>
import mixinUser from '@/mixins/mixinUser'
import { requestBuilder, deepUpdate, takeLabelByKey } from '@/utils/util'
import * as personApi from '@/api/person'

export default {
  name: 'PersonTableDrawer',
  mixins: [mixinUser],
  props: {
    queryOptions: {
      type: Object,
      default: function () {
        return {
          orgTree: [],
          departmentSysId: [],
          roleId: []
        }
      }
    }
  },
  data () {
    return {
      // 
      title: '',
      visible: false,

      // 
      queryParam: {
        personName: '',
        isUser: 'N',
        activity: 'Y',
        orgId: '',
        range: 'all'
      },

      // 
      columns: [
        {
          title: '',
          dataIndex: 'personName',
          ellipsis: true,
          width: 120
        },
        {
          title: '',
          dataIndex: 'mobilePhone',
          ellipsis: true,
          width: 120
        },
        {
          title: '',
          dataIndex: 'orgId',
          scopedSlots: { customRender: 'orgId' },
          ellipsis: true,
          width: 110
        },
        {
          title: '',
          dataIndex: 'isUser',
          scopedSlots: { customRender: 'isUser' },
          align: 'center',
          width: 90
        }
      ],
      scroll: {
        x: '100%',
        scrollToFirstRowOnChange: false
      },
      rowSelection: {
        type: 'radio',
        onChange: this.doTableChange,
        getCheckboxProps: record => {
          return {
            props: {
              disabled: record.isUser === 'Y'
            }
          }
        }
      },
      dataSource: [],
      selectedRows: [],
      selectedRowKeys: [],
      clearSelection: true,
      showPagination: true,
      immediate: true,
      bordered: false,
      loadData: parameter => {
        const param = requestBuilder(
          '',
          deepUpdate(
            {
              personName: '',
              activity: 'Y',
              isUser: '',
              orgId: '',
              range: ''
            },
            {
              ...this.queryParam,
              orgId: this.userInfo.orgId
            }
          ),
          parameter.pageNo,
          parameter.pageSize,
          parameter.sortField,
          parameter.sortOrder
        )
        return personApi.getPersonInfoList(param).then(res => {
          if (res.code !== '0000') {
            this.$notification.error({
              message: '',
              description: res.message || '!'
            })
            return Promise.reject(res)
          }

          // 
          this.dataSource = res.result.data

          // 
          return res
        })
      },
      rowClick: record => ({
        on: {}
      }),
      rowClassName: record => {
        return ''
      },

      // 
      pagination: {
        total: 0,
        current: 1,
        pageSize: 20,
        pageSizeOptions: ['10', '20', '25', '30', '50', '100', '200'],
        showTotal: (total, range) => ` ${range[0]}-${range[1]}  ( ${total} )`,
        hideOnSinglePage: false,
        showSizeChanger: true,
        showLessItems: true
      }
    }
  },
  mounted () {
    this.doResizeObserve()
  },
  destroyed () {
    this.doResizeDestroy()
  },
  methods: {
    /**
     * @description 
     * @param {Array} tree
     * @param {String | Number} key
     * @param {String} value?
     * @param {String} children?
     * @return {String}
     */
    takeLabelByKey,

    /**
     * @description 
     * @returns {undefined}
     */
    doResizeObserve () {
      if (window.ResizeObserver) {
        const optimize = () => {
          return () => {
            const $table = this.$refs.table.$el
            const $parent = $table.parentElement
            const $thead = $table.querySelector('.ant-table-fixed > .ant-table-thead')
            const parentHeight = ($parent && $parent.clientHeight) || 0
            const theadHeight = ($thead && $thead.clientHeight) || 0
            const scrollHeight = parentHeight - theadHeight

            if (scrollHeight) {
              this.scroll = {
                ...this.scroll,
                y: scrollHeight
              }
            }
          }
        }
        this.resizeObserver = new window.ResizeObserver(optimize())
        this.resizeObserver.observe(this.$refs.table.$el.parentElement)
      }
    },

    /**
     * @description 
     * @returns {undefined}
     */
    doResizeDestroy () {
      if (window.ResizeObserver && this.resizeObserver) {
        this.resizeObserver.disconnect()
      }
    },

    /**
     * @description 
     * @param {Array <String>} selectedRowKeys
     * @param {Array <Object>} selectedRows
     * @returns {undefined}
     */
    doTableChange (selectedRowKeys, selectedRows) {
      this.selectedRows = selectedRows
      this.selectedRowKeys = selectedRowKeys
    },

    /**
     * @description 
     * @returns {undefined}
     */
    doTableQuery () {
      this.$refs.table.refresh(true)
    },

    /**
     * @description 
     * @returns {undefined}
     */
    doTableClear () {
      this.$refs.table.clear()
    },

    /**
     * @description 
     * @returns {undefined}
     */
    doOpen () {
      // 
      this.title = ''
      this.visible = true

      // 
      this.queryParam = {
        personName: '',
        isUser: 'N',
        activity: 'Y',
        orgId: '',
        range: 'all'
      }

      // 
      this.dataSource = []
      this.selectedRows = []
      this.selectedRowKeys = []

      // 
      this.doTableQuery()
    },

    /**
     * @description 
     * @returns {undefined}
     */
    doClose () {
      if (this.visible) {
        // 
        this.title = ''
        this.visible = false

        // 
        this.queryParam = {
          personName: '',
          isUser: 'N',
          activity: 'Y',
          orgId: '',
          range: 'all'
        }

        // 
        this.dataSource = []
        this.selectedRows = []
        this.selectedRowKeys = []

        // 
        this.doTableClear()

        // 
        this.$emit('closed')
      }
    },

    /**
     * @description 
     * @returns {undefined}
     */
    doSubmit () {
      if (this.selectedRows.length === 0) {
        this.$message.error('')
        return
      }

      // 
      const [record = {}] = this.selectedRows

      // 
      this.doClose()
      this.$emit('submitted', record)
    }
  }
}
</script>
